import { Injectable } from '@angular/core';
import { ActionSheetController } from '@ionic/angular';
import { Model } from '../api/models/model';
import { RequestService } from '../api/request.service';
import { AuthUserService } from './auth-user.service';

@Injectable({
  providedIn: 'root'
})
export class ActionSheetCategoryService {

  modelCategory : Model;
  modelUser : Model;
  categories : [] =  null;
  public category : any;
  actionSheet = null;


  constructor(
    public actionSheetController: ActionSheetController,
    public requestService : RequestService,
    public authUser : AuthUserService
  ) {
    this.modelCategory = new Model('Category',requestService);
    this.modelUser = new Model('User',requestService);

    this.authUser.userOb.subscribe(hasUser => {
      if(hasUser)
        this.init();
    })

    }

  async init(){
    console.log('init categories')
    if(this.categories == null){
      await this.modelCategory.api_function('all').subscribe(response =>{
        if(response['status'] == 'success'){
          this.categories = response['data'];
          
          //this.mustSelect();
        }
      },error => {
        alert(error.response.message);
      })
    }
  }

  mustSelect(){

    if(!this.authUser.hasSelectCategory()){
       this.showActionCategory();
    }else{
      this.category = this.authUser.user.category;
    }

  }

  async showActionCategory(){


    let buttons = [];

    if(!Array.isArray(this.categories)){
      await this.init();
      
      return;
    }

    this.categories.map( (category : any) => {
      buttons.push({
        text: category.name,
        role: 'destructive',
        id: category.id,
        data: {
          type: ''
        },
        handler: () => {
          this.selectCategory(category);
        }
      })
    })

    if(this.actionSheet != null){
      return;
    }

    this.actionSheet = await this.actionSheetController.create({
      header: 'Categorías',
      cssClass: 'my-custom-class',
      buttons: buttons
    });

    await this.actionSheet.present();

    const { role, data } = await this.actionSheet.onDidDismiss();
    console.log(data);

    this.actionSheet = null;
  }


  selectCategory(category : any){
    if( this.category && this.category.name == category.name){
      return;
     }

    this.modelUser.api_functionModel(this.authUser.user.id,'setCategory',{'id':category.id}).subscribe(
      async (response : any) => {
        
        this.category = category;
        await this.authUser.setCategory(this.category);
        window.location.reload();
        
      },
      error =>{
        alert('error al seleccionar categoría')
      }
    )
  }

}
